  margin: 0 auto !important;
  overflow: hidden !important;
}

.seccion {
  margin: 0 auto !important;
  overflow: hidden !important;
}

a,
button {
  text-decoration: none !important;
  border-style: none;
  color: #333;
}

img,
picture {
  width: 100%;
