header h1 {
  font-weight: 100;
  font-size: 4rem;
  letter-spacing: 0.3rem;
  margin-top: 0;
  text-align: center;
  padding-top: 6rem;
}
@media (min-width: 768px) {
  header h1 {
    font-size: 5rem;
    padding-top: 10rem;
  }
}
header .timeline {
  position: relative;
  max-width: 1200;
  margin: 1rem;
}
@media (min-width: 1024px) {
  header .timeline {
    margin: 5rem;
  }
}
header .timeline .container {
  position: relative;
  width: 100%;
  padding-left: 8rem;
  padding-right: 2.5rem;
  margin: 5rem 0;
}
@media (min-width: 1024px) {
  header .timeline .container {
    width: 50%;
    padding: 1rem 5rem;
    margin: 0;
  }
}
header .timeline .container img {
  width: 5rem;
  height: 5rem;
  position: absolute;
  right: -3rem;
  top: 3.2rem;
  z-index: 10;
}
header .timeline .container .text-box {
  padding: 2rem 3rem;
  background: #333;
  position: relative;
  border-radius: 0.6rem;
  font-size: 1.2rem;
  box-shadow: 0px 10px 15px 0px rgb(156, 156, 156);
}
@media (min-width: 1024px) {
  header .timeline .container .text-box {
    font-style: 1.5rem;
  }
}
header .timeline .container .text-box h2 {
  text-align: left;
  font-weight: bold;
  font-size: 2rem;
  margin-bottom: 1.5rem;
  color: #fff;
}
header .timeline .container .text-box small {
  display: inline-block;
  margin-bottom: 0.5rem;
  font-size: 1.5rem;
  font-weight: 100;
  color: #eedeaa;
}
@media (min-width: 1024px) {
  header .timeline .container .text-box small {
    margin-bottom: 1.5rem;
  }
}
header .timeline .container .text-box li {
  color: #fff;
  margin-bottom: 1rem;
  font-weight: 200;
  margin-left: 2rem;
}
header .timeline .container .text-box li:nth-child(1) {
  margin-top: 1rem;
}
header .timeline .container .text-box p {
  font-size: 1.5rem;
  font-weight: 200;
  color: #fff;
}
header .timeline .container .text-box .left-container-arrow {
  height: 0;
  width: 0;
  position: absolute;
  top: 2.8rem;
  z-index: 1;
  border-top: 1.5rem solid transparent;
  border-bottom: 1.5rem solid transparent;
  border-right: 1.5rem solid;
  left: -1.5rem;
}
@media (min-width: 1024px) {
  header .timeline .container .text-box .left-container-arrow {
    border-left: 1.5rem solid #333;
    border-right: 0;
    right: -1.5rem;
    left: 100%;
  }
}
header .timeline .container .text-box .right-container-arrow {
  height: 0;
  width: 0;
  position: absolute;
  top: 2.8rem;
  z-index: 1;
  border-top: 1.5rem solid transparent;
  border-bottom: 1.5rem solid transparent;
  border-right: 1.5rem solid #333;
  left: -1.5rem;
}
header .timeline .container.left-container {
  left: 0;
}
header .timeline .container.left-container img {
  left: 1rem;
}
@media (min-width: 1024px) {
  header .timeline .container.left-container img {
    left: auto;
    right: -3rem;
  }
}
header .timeline .container.right-container {
  left: 0;
}
@media (min-width: 1024px) {
  header .timeline .container.right-container {
    left: 50%;
  }
}
header .timeline .container.right-container img {
  left: 1rem;
}
@media (min-width: 1024px) {
  header .timeline .container.right-container img {
    left: -2rem;
  }
}
header .timeline::after {
  content: "";
  position: absolute;
  top: 0;
  left: 3rem;
  width: 0.6rem;
  height: 100%;
  background-color: #555555;
  margin-left: 0.3rem;
  z-index: 0;
}
@media (min-width: 1024px) {
  header .timeline::after {
    left: 50%;
  }
}

.excep {
  background-color: transparent !important;
  height: 25rem;
}
.excep:hover h2 {
  opacity: 0;
}
.excep h2 {
  position: absolute;
  z-index: 2;
  pointer-events: none;
  transition: 0.2s;
}
.excep .fondo {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 0.6rem;
}
@media (min-width: 480px) {
  .excep .fondo {
    flex-direction: row;
  }
}
.excep .fondo p {
  position: relative;
  width: 100%;
  height: 50%;
  overflow: hidden !important;
  transition: all 0.4s;
  display: grid;
  place-content: center;
}
@media (min-width: 480px) {
  .excep .fondo p {
    width: 50%;
    height: 100%;
  }
}
.excep .fondo p a {
  position: relative;
  text-align: center;
  z-index: 10;
  color: #eedeaa;
  margin-top: 15rem;
  transition: 0.3s;
  font-size: 1.5rem;
  letter-spacing: 0.3rem;
  font-weight: 600;
  opacity: 0;
}
.excep .fondo p a::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 0.1rem;
  background-color: #eedeaa;
  transition: all 0.3s;
}
.excep .fondo p a:hover::after {
  left: 0;
  width: 100%;
}
.excep .fondo p:hover {
  transform: scale(1.1);
}
.excep .fondo p:hover a {
  opacity: 1;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .excep .fondo p:hover a {
    margin-top: 8rem;
  }
}
.excep .fondo p::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: 0 auto;
  display: grid;
  place-content: center;
  transition: all 0.3s;
  opacity: 0;
  font-size: 1.5rem;
  letter-spacing: 0.3rem;
}
.excep .fondo p::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: 0 auto;
  display: grid;
  place-content: center;
  transition: 0.3s;
  opacity: 0;
  font-size: 2rem;
}
.excep .fondo p:hover::after {
  transform: translateX(0);
  opacity: 1;
}
.excep .fondo p:hover::before {
  transform: translateY(5rem);
  opacity: 1;
}
.excep .fondo .p1 {
  background: linear-gradient(rgba(0, 0, 0, 0.651), rgba(0, 0, 0, 0.75)), url("/build/img/sahumos.jpg") no-repeat center center;
  background-size: cover;
}
.excep .fondo .p1::after {
  content: "Sahumerios";
  transform: translateX(-8rem);
}
.excep .fondo .p2 {
  background: linear-gradient(rgba(0, 0, 0, 0.651), rgba(0, 0, 0, 0.75)), url("/build/img/flor-blanca.jpg") no-repeat center center;
  background-size: cover;
}
.excep .fondo .p2::after {
  content: "Plantas";
  transform: translateX(8rem);
}

