  place-content: center;
  text-align: center;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  background-color: #ff3f3f;
  color: #fff;
  left: -1rem;
  top: 10rem;
  animation: contador 1.5s linear infinite both;
}
